import React, { Component } from 'react';
import { Link, navigate } from "gatsby"
import wordmark from '../../public/rivian_wordmark.png'

export default class extends Component {
  constructor(props) {
    super(props);

    const navigation = [
      {
        name: "Rivian",
        path: "/"
      },
      {
        name: "R1T",
        path: "/r1t/"
      },
      {
        name: "R1S",
        path: "/r1s/"
      },
      {
        name: "Technology",
        path: "/technology/"
      },
      {
        name: "About",
        path: "/about/"
      }
    ]

    this.state = {
      windowHeight: 0,
      sticky: false,
      navigation
    }

    this.measureHighlight = this.measureHighlight.bind(this);
    this.navigateNext = this.navigateNext.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
  }

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown);

    setTimeout(() => {
      this.measureHighlight(this.props.location.pathname);
    }, 2000);
  }

  componentWillReceiveProps(nextProps) {
    this.measureHighlight(nextProps.location.pathname);
  }

  measureHighlight(pathname) {
    // getBoundingClientRect

    let children = this.links.children;

    for (const [index, child] of Object.entries(children)) {
      if (child && child.dataset.path === pathname) {
        let width = child.getElementsByTagName('p')[0].clientWidth;

        this.setState({
          activeItemWidth: width,
          activeItemOffset: child.offsetLeft
        });
      }
    }




    // // let nextActive = children.filt
    // for (const [index, child] of Object.entries(children)) {

    //   if (child && child.classList.contains('active')) {
    //     console.log("next", this.props.location.pathname, child, child.getElementsByTagName('p')[0].clientWidth)
    //     if (this.state.activeItemWidth !== child.getElementsByTagName('p')[0].clientWidth) {
    //       this.setState({
    //         activeItemWidth: child.getElementsByTagName('p')[0].clientWidth,
    //         activeItemOffset: child.offsetLeft
    //       });
    //     }


    //     // getElementsByTagName('a')[0]
    //   }

    // }
  }

  navigateNext(direction) {
    let children = this.links.children;

    for (const [index, child] of Object.entries(children)) {

      if (child && child.classList.contains('active')) {

        if (direction === "left") {
          if (index > 0) {
            navigate(this.state.navigation[index - 1].path + "/");
          }
        } else if (direction === "right") {
          if (index < this.state.navigation.length - 1) {
            navigate(this.state.navigation[parseInt(index) + 1].path + "/");
          }
        }
      }
    }
  }

  handleClick(el) {
    this.navigateNext(el.currentTarget.dataset.direction);
  }

  handleKeyDown(key) {
    if (key.code) {
      if (key.code === "ArrowRight") {
        this.navigateNext("right");
      } else if (key.code === "ArrowLeft") {
        this.navigateNext("left");
      }
    }
  }

  render() {
    let highlightStyle = {
      width: this.state.activeItemWidth,
      marginLeft: `calc(${this.state.activeItemOffset}px + 3rem)`
    }

    return (
      <React.Fragment>
        <nav>
          <Link className="rm_nav-home" to="/"><img alt="wordmark" src={wordmark} /></Link>
          <ul ref={links => this.links = links} className={`rm_nav-dock`}>
            {this.state.navigation.map(item => <Link key={item.name} data-path={item.path} className="rm_nav-link" activeClassName="active" to={item.path}><p>{item.name}</p></Link>)}
            <hr style={highlightStyle}  />
          </ul>
        </nav>
        {/*<div className="rm_nav-controls">
          <button className="button arrow-right" data-direction="right" onClick={this.handleClick}>
            <span></span>
          </button>
        </div>*/}

      </React.Fragment>
    )
  }
}
